.app__works{
    flex:1;
    width: 100%;
    flex-direction: column;
}

.app__work-filter {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 4rem 0 2rem;

    .app__work-filter-item {
        padding: 0.5rem 1rem;
        border-radius: 0.5rem;
        background: #ffffff;
        color: #000;
        font-weight: 800;
        cursor: pointer;
        transition: all 0.3s ease;
        margin: 0.5rem;

        &:hover {
            background: var(--secondary-color);
            color: #ffffff;
        }

        @media screen and (min-width:2000px) {
            padding: 1rem 2rem;
            border-radius: 0.85rem;            
        }
    }

    .item-active{
        background: var(--secondary-color);
        color: #ffffff;
    }
}

.app__work-portfolio{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    .app__work-item{
        width: 370px;
        flex-direction: column;
        margin: 2rem;
        padding: 1rem;
        border-radius:  0.5rem;
        background-color: #ffffff;
        color: #000;
        cursor: pointer;
        transition: all 0.3s ease;

        &:hover{
            box-shadow: 0 0 25px rgb(0, 0, 0, 0.2);
        }

        @media screen and (min-width:2000px) {
            width: 470px;
            padding: 1.25rem;
            border-radius: 0.75rem;
        }

        @media screen and (max-width:260px) {
            width: 100%;
            margin: 1rem;
        }
    }
}

.app__work-img {
    width: 100%;
    height: 230px;
    position: relative;

    img {
        width: 100%;
        height: 100%;
        border-radius: 0.5rem;
        object-fit: cover;
    }

    @media screen and (min-width:2000px) {
        height: 350px;
    }
}

.app__work-hover{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);

    border-radius: 0.5rem;
    opacity: 0;
    transition: all 0.3s ease;

    div {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: rgba(0,0,0,0.5);
        color: #ffffff;

        margin: 1rem;
        font-family: var(--font-base);
        font-weight: 800;
        cursor: pointer;
        transition: all 0.3s ease;

        svg {
            width: 50%;
            height: 50%;
            color: #ffffff;
        }
    }
}

.app__work-content{
    padding: 0.5rem;
    width: 100%;
    position: relative;
    flex-direction: column;

    h4{
        margin-top: 1rem;
        line-height: 1.5;
        
        @media screen and (min-width: 2000px) {
            margin-top: 3rem;
          }
    }

    .app__work-tag{
        position:absolute;
        padding: 0.5rem 1rem;
        border-radius: 10px;
        background-color: #ffffff;
        top:-25px;
    }
}